'use client'

import React, { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import { getToken, isAuthenticated } from "@/lib/utils/auth";
import { SidebarComponent } from "@/components/Sidebar/SidebarComponent";
import ProfileUserDetails from "@/components/ProfileComponents/ProfileUserDetails";
import ReqUserPostPart from "@/components/ProfileComponents/ReqUserPostPart";
import { fetchUserInfo, getFollowers, getFollowings } from "@/lib/api/userApi";
import { fetchUserPosts } from "@/lib/api/postApi";
import { Post, UserProfile } from "@/types/api.types";

export default function ProfilePage() {
    const router = useRouter();
    const [posts, setPosts] = useState<Post[]>([]);
    const [user, setUser] = useState<UserProfile | null>(null);
    const [followers, setFollowers] = useState<UserProfile[]>([]);
    const [followings, setFollowings] = useState<UserProfile[]>([]);
    const [isLoading, setIsLoading] = useState(true);
    const [isMobile, setIsMobile] = useState(false);
    const [sidebarOpen, setSidebarOpen] = useState(false);

    // Check authentication
    useEffect(() => {
        if (typeof window === 'undefined') return;
        if (!isAuthenticated()) {
            router.push('/login');
        }
    }, [router]);

    // Check mobile
    useEffect(() => {
        if (typeof window === 'undefined') return;
        const checkMobile = () => {
            setIsMobile(window.innerWidth < 768);
        };
        checkMobile();
        window.addEventListener('resize', checkMobile);
        return () => window.removeEventListener('resize', checkMobile);
    }, []);

    // Fetch data
    useEffect(() => {
        const token = getToken();
        if (!token) return;

        const getUserInfo = async () => {
            try {
                setIsLoading(true);
                const result = await fetchUserInfo();
                setUser(result);

                if (result?.id) {
                    const [followerData, followingData, userPosts] = await Promise.all([
                        getFollowers(result.id),
                        getFollowings(result.id),
                        fetchUserPosts(),
                    ]);
                    setFollowers(followerData || []);
                    setFollowings(followingData || []);
                    setPosts(userPosts || []);
                }
            } catch (error) {
                console.log("Error fetching user:", error);
            } finally {
                setIsLoading(false);
            }
        };

        getUserInfo();
    }, []);

    return (
        <div className="flex min-h-screen">
            {/* Mobile menu button */}
            {isMobile && (
                <button
                    onClick={() => setSidebarOpen(!sidebarOpen)}
                    className="fixed top-4 left-4 z-50 p-2 bg-white rounded-md shadow-md border md:hidden"
                    aria-label="Toggle menu"
                >
                    <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        {sidebarOpen ? (
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                        ) : (
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
                        )}
                    </svg>
                </button>
            )}

            {/* Mobile overlay */}
            {isMobile && sidebarOpen && (
                <div
                    className="fixed inset-0 bg-black bg-opacity-50 z-40 md:hidden"
                    onClick={() => setSidebarOpen(false)}
                />
            )}

            {/* Sidebar - Desktop */}
            <div className="hidden md:block w-[240px] h-screen bg-white border-r flex-shrink-0">
                <SidebarComponent />
            </div>

            {/* Sidebar - Mobile */}
            <div
                className={`
          md:hidden fixed top-0 left-0 w-64 h-screen bg-white border-r z-50
          transition-transform duration-300 ease-in-out
          ${sidebarOpen ? 'translate-x-0' : '-translate-x-full'}
        `}
            >
                <SidebarComponent />
            </div>

            {/* Main Content */}
            <div className={`flex-1 min-h-screen w-full max-w-full overflow-x-hidden ${isMobile ? 'pt-16 px-4' : 'pl-3'}`}>
                <div className="min-h-screen bg-gradient-to-b from-gray-50 to-white">
                    <div className="max-w-4xl mx-auto px-4">
                        {/* Profile Header - Using Component */}
                        <ProfileUserDetails
                            user={user}
                            posts={posts}
                            followers={followers}
                            followings={followings}
                            isLoading={isLoading}
                        />

                        {/* User Posts - Using Component */}
                        <div className="pb-8">
                            <h3 className="text-xl font-bold text-gray-800 mb-4 px-4">Your Posts</h3>
                            <ReqUserPostPart posts={posts} isLoading={isLoading} />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
